const MyVal=(prop)=>
{
        const[yearname,changeFilt]=useState('');
   function cha(year)
   {
      changeFilt(year); 
      prop.onch(year);
     }
   var fliterarray=prop.nam.filter((word)=>{return ((word.date.getYear()+1900).toString())===yearname});   
   console.log(fliterarray);
   return (  
    <div className="expenses">
       <Expensefil onYearChan={cha}></Expensefil>
       <Expenses expense={fliterarray}/>
       {fliterarray.length>0?fliterarray.map((expense)=>
       <MySet 
       date={expense.date}
       name={expense.name} 
       val={expense.val} 
      />):<p className="para">No expense...</p>}
   </div>
   );
}
export default MyVal;