import Chart from './Chart' 
function Expenses(props)
{
   const data=[
       {
           label:'jan',
           value:0
       },
       {
        label:'feb',
        value:0
    },
    {
        label:'mar',
        value:0
    },
    {
        label:'apr',
        value:0
    },
    {
        label:'may',
        value:0
    },
    {
        label:'jun',
        value:0
    },
    {
        label:'jul',
        value:0
    },
    {
        label:'aug',
        value:0
    },
    {
        label:'sep',
        value:0
    },
    {
        label:'oct',
        value:0
    },
    {
        label:'nov',
        value:0
    },
    {
        label:'dec',
        value:0
    }
   ]
  console.log(data); 
  for (const expense of props.expense)
  {
      const month=expense.date.getMonth();
      console.log(month);
      data[month].value+=expense.val;
  }
  console.log(data); 
  return (
   <Chart data={data}></Chart>
  );
}
export default Expenses;